#root {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.hot-rod-app {
  display: flex;
  width: 100%;
  height: 100%;
}

.hot-rod-simulation {
  width: calc(50% - 40px);
  height: calc(100% - 40px);
  padding: 20px;
}

.hot-rod-code-editor {
  width: calc(50% - 40px);
  height: calc(100% - 40px);
  padding: 20px;
  background-color: black;
  color: white;
}

#init-code-editor {
  color: white;
  border: none;
  appearance: none;
  color: white;
  outline: none;
  width: 400px;
  max-width: 100%;
  margin-top: 20px;
  height: 100px;
  background-color: rgba(255, 255, 255, 0.1);
  resize: none;
  padding: 12px;
}

#code-editor {
  color: white;
  border: none;
  appearance: none;
  color: white;
  outline: none;
  width: 400px;
  max-width: 100%;
  margin-top: 20px;
  height: 400px;
  background-color: rgba(255, 255, 255, 0.1);
  resize: none;
  padding: 12px;
}

.run-code {
  float: right;
}
.reset-code {
  position: absolute;
  right: 20px;
  top: 20px;
}

.next-level {
  float: right;
  color: green;
}

.hot-rod-grid {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  align-items: center;
  justify-content: center;
  height: calc(100% - 20px);
}
.row {
  display: flex;
  align-items: center;
}

.cell {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border: 1px solid black;
  margin-left: -1px;
  margin-top: -1px;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
}

.cell-W {
  background-color: rgba(66, 66, 66, 1);
}

.cell-S {
  background-color: rgba(0, 0, 255, 0.5);
}

.cell-E {
  background-color: rgba(0, 255, 0, 0.5);
}

.hot-rod {
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background-color: red;
}

.pedestrian {
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background-color: black;
}

.pedestrian-highlighted {
  background-color: purple;
  height: 20px;
  width: 20px;
  position: absolute;
  z-index: -1;
}

.level-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border: 1px solid gray;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
  white-space: nowrap;
}

.level-container-clickable {
  cursor: pointer;
}
.level-container-clickable:hover {
  background-color: black !important;
  color: white !important;
}

.world-container {
  position: relative;
}

.hot-rod-world {
  position: relative;
  width: calc(75% - 40px);
  height: calc(100% - 40px);
  padding: 20px;
}

.hot-rod-world-info {
  width: calc(25% - 40px);
  height: calc(100% - 40px);
  padding: 20px;
  background-color: black;
  color: white;
}

.hot-rod-world-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.hot-rod-world-info p {
  font-size: 13px;
  color: #ccc;
  line-height: 1.5em;
}
